<template>
  <Modal
    class="logout_modal"
    :can_continue="true"
    :title="'Log Out'"
    :html="html"
    :continue_action="logout"
    :skippable="true"
    @skip="$emit('close')"
    :skip_text="'Cancel'"
    :submit_text="'Confirm'"
  />
</template>

<script>
import { useMainStore } from "@/store";
export default {
  name: "LogoutModal",
  emits: ["close"],
  data() {
    return {
      html: `<div class="overlay_contents_text">Are you sure you want to log out?</div>`,
    };
  },
  methods: {
    logout() {
      useMainStore().logout();
    },
  },
};
</script>

<style></style>
